*{margin:0;padding:0;box-sizing:border-box}body{width:100%;height:100vh;margin:0 auto;overflow:clip;background-color:blue}.App{display:flex;justify-content:flex-start;margin:200px auto;background-color:#9a229a;padding:30px;width:50%;border-radius:25%}.App #drum-machine{display:flex}.App #drum-machine .drum-pads{display:grid;grid-template-columns:1fr 1fr 1fr}.App #drum-machine .drum-pads .drum-pad{border:1px solid #000;width:100px;height:100px;display:flex;justify-content:center;align-items:center;border-radius:100%;background-color:green;font-weight:bold}.App #drum-machine .controls{display:inline-block;margin:auto 70px}.App #drum-machine .controls .power{display:flex;justify-content:center;font-size:25px;background-color:#9a229a}.App #drum-machine .controls .power input[type=checkbox].toggle{opacity:0;position:absolute;left:-9000px;top:-9000px}.App #drum-machine .controls .power input[type=checkbox].toggle+span{display:flex;position:relative;align-items:center;cursor:pointer}.App #drum-machine .controls .power input[type=checkbox].toggle+span::before{content:"";width:2em;height:1em;background-color:#fad1d1;border-radius:1em;margin-right:.25em;transition:200ms ease-in-out}.App #drum-machine .controls .power input[type=checkbox].toggle+span::after{display:flex;justify-content:center;align-items:center;position:absolute;left:.1em;content:"✕";font-size:.5em;height:.9em;left:.2em;width:1.8em;height:1.8em;background-color:#eb4747;color:#fff;border-radius:1em;transition:200ms ease-in-out}.App #drum-machine .controls .power input[type=checkbox].toggle:checked+span::before{background-color:#e0f7d4}.App #drum-machine .controls .power input[type=checkbox].toggle:checked+span::after{content:"✓";transform:translate(100%);background-color:#81e052}.App #drum-machine .controls #display{display:flex;justify-content:center;align-items:center;background-color:aqua;width:300px;height:20px;padding:30px;margin:30px auto;border-radius:10px;font-weight:bold}.App #drum-machine .controls h3{text-align:center}.App #drum-machine .controls .sound{background-color:red;margin:0px;width:100%;height:20px}